.header {
  background: rgba(255, 255, 255, 0.5);
  box-shadow: 0 0 16px 0 rgba(74, 86, 91, 0.6);
  backdrop-filter: blur(15px);
  padding: 30px 0;
  color: #0F2A44;
  .container {
    max-width: 1410px;
  }
  &__desktop {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
  }
  &__logo {
    width: 216px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    li:not(:last-child) {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(0, -50%);
        width: 2px;
        height: 40px;
        background: #0F2A44;
        border-radius: 3px;
      }
    }
    li a {
      padding: 0 32px;
      font-size: 18px;
      line-height: 155%;
      font-weight: 700;
      letter-spacing: 1.26px;
      text-transform: uppercase;
    }
  }
  &__phone {
    font-size: 18px;
    line-height: 155%;
    font-weight: 700;
    letter-spacing: 1.26px;
    text-transform: uppercase;
  }
  &__time {
    padding-left: 10px;
    font-size: 18px;
    line-height: 155%;
    font-weight: 900;
    letter-spacing: 1.26px;
    text-transform: uppercase;
  }
  &__mob {
    display: none;
  }
}

@media screen and (max-width: 1400px) {
  .header {
    &__desktop {
      gap: 16px;
    }
    &__logo {
      width: 120px;
    }
    &__list {
      li:not(:last-child) {
        &:after {
          width: 1px;
          height: 24px;
        }
      }
      li a {
        padding: 0 16px;
        font-size: 15px;
        letter-spacing: 1.05px;
      }
    }
    &__phone,
    &__time {
      font-size: 15px;
      letter-spacing: 1.05px;
    }
  }
}

@media screen and (max-width: 1023px) {
  .header {
    position: absolute;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100%;
    padding: 15px 0;
    &__desktop {
      position: fixed;
      z-index: 10;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      min-height: 100vh;
      flex-direction: column;
      align-items: start;
      gap: 24px;
      background: #F5FAFF;
      padding: 15px 15px 30px 15px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
    }
    &__logo {
      width: 150px;
    }
    &__list {
      flex-direction: column;
      gap: 20px;
      li:not(:last-child) {
        &:after {
          display: none;
        }
      }
    }
    &__mob {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 16px;
    }
  }
  .burger {
    position: relative;
    z-index: 12;
    width: 22px;
    height: 16px;
    cursor: pointer;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      display: block;
      background: #0F2A44;
      border-radius: 3px;
      transition: all 0.3s;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
    &--active {
      span {
        border-radius: 0;
      }
      span:nth-child(1) {
        top: 7px;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        display: none;
      }
      span:nth-child(3) {
        bottom: 7px;
        transform: rotate(45deg);
      }
    }
  }
}