.main {
  background-image: url("../img/main-img.webp");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  padding: 170px 0 80px;
  &__title {
    font-size: 60px;
    line-height: 125%;
    font-weight: 800;
    letter-spacing: 6px;
    text-transform: uppercase;
    margin-bottom: 24px;
    span {
      font-weight: 500;
    }
  }
  &__text {
    font-size: 24px;
    line-height: 120%;
    font-weight: 500;
    margin-bottom: 24px;
  }
  &__location {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-bottom: 45px;
    i {
      width: 21px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 24px;
      line-height: 120%;
      font-weight: 500;
    }
  }
  &__btns {
    display: flex;
    align-items: stretch;
    gap: 35px;
  }
  &__btn {
    min-height: 52px;
    padding: 5px 20px;
    span {
      font-size: 16px;
      letter-spacing: 1px;
    }
  }
}
.advantages {
  position: relative;
  padding: 120px 0 80px;
  .container {
    position: relative;
    z-index: 2;
  }
  &__group {
    position: relative;
    margin-bottom: 60px;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: -30px;
      width: 6px;
      height: 100%;
      background: #2EC5FF;
    }
  }
  &__title {
    font-size: 40px;
    line-height: 120%;
    font-weight: 800;
    letter-spacing: 4px;
    text-transform: uppercase;
    color: #0F2A44;
    margin-bottom: 40px;
  }
  &__text {
    max-width: 890px;
    font-size: 24px;
    line-height: 160%;
    font-weight: 500;
    letter-spacing: 0.48px;
    span {
      font-weight: 800;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 120px / 4));
    gap: 40px;
  }
  &__item {
    text-align: center;
    &-icon {
      height: 150px;
      margin: 0 auto 8px auto;
      img {
        max-width: 100%;
        max-height: 100%;
      }
    }
    &-title {
      font-size: 24px;
      line-height: 120%;
      font-weight: 700;
      margin-bottom: 8px;
    }
    &-text {
      font-size: 18px;
      line-height: 130%;
      font-weight: 500;
    }
  }
  &__icon,
  &__icon2,
  &__icon3,
  &__icon4 {
    position: absolute;
    z-index: 1;
    pointer-events: none;
    img {
      max-width: 100%;
    }
  }
  &__icon {
    top: 80px;
    right: 320px;
    max-width: 83px;
  }
  &__icon2 {
    top: 104px;
    right: 83px;
    max-width: 106px;
  }
  &__icon3 {
    top: 268px;
    right: 242px;
    max-width: 281px;
  }
  &__icon4 {
    top: 358px;
    right: 0;
    max-width: 100px;
  }
}
.tariff {
  padding-bottom: 40px;
  &__group {
    background-image: url("../img/tariff-img.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 60px 0;
  }
  &__title {
    text-align: center;
    color: #0F2A44;
    margin-bottom: 24px;
  }
  &__items {
    display: flex;
    justify-content: center;
  }
  &__item {
    position: relative;
    text-align: center;
    padding: 0 72px;
    &:not(:last-child) {
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(0, -50%);
        width: 3px;
        height: 40px;
        background: #0F2A44;
        border-radius: 3px;
      }
    }
    &-title {
      font-size: 24px;
      line-height: 120%;
      font-weight: 700;
      letter-spacing: 1.68px;
      text-transform: uppercase;
      margin-bottom: 16px;
    }
    &-text {
      font-size: 24px;
      line-height: 155%;
      font-weight: 900;
      letter-spacing: 1.68px;
      text-transform: uppercase;
      span {
        font-weight: 500;
      }
    }
  }
  &__info {
    text-align: center;
    padding: 0 40px;
    font-size: 18px;
    line-height: 120%;
    font-weight: 500;
    letter-spacing: 0.36px;
    margin: 20px auto 0;
  }
}
.schedule {
  padding: 40px 0 80px;
  &__inner {
    max-width: 1240px;
    text-align: center;
    background: linear-gradient(190.75deg, rgba(255, 255, 255, 1),rgba(245, 250, 255, 1) 100%);
    box-shadow: 0px 8px 16px 0px rgba(211, 219, 245, 1);
    border-radius: 20px;
    padding: 60px 40px;
    margin: 0 auto;
  }
  &__title {
    margin-bottom: 24px;
  }
  &__subtitle {
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
    margin-bottom: 24px;
  }
  &__items {
    max-width: 845px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
    gap: 36px;
    margin: 0 auto 24px auto;
  }
  &__item {
    min-width: 182px;
    min-height: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #B9EBFF;
    border-radius: 10px;
    padding: 10px;
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
    letter-spacing: 1.68px;
    &:nth-child(2) {
      min-width: 190px;
    }
  }
  &__info {
    font-size: 18px;
    line-height: 120%;
    font-weight: 500;
    letter-spacing: 0.36px;
  }
}
.rent {
  position: relative;
  text-align: center;
  background-image: url("../img/rent-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 60px 0;
  &__title {
    margin-bottom: 10px;
  }
  &__subtitle {
    font-size: 24px;
    line-height: 120%;
    font-weight: 900;
    letter-spacing: 1.68px;
    text-transform: uppercase;
    margin-bottom: 24px;
    span {
      font-weight: 500;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 72px / 4));
    gap: 24px;
    margin-bottom: 40px;
  }
  &__item {
    min-height: 240px;
    background: rgba(255, 255, 255, 0.6);
    box-shadow: 0px 8px 24px 0px rgba(0, 0, 0, 0.08);
    border-radius: 20px;
    padding: 30px 20px 50px;
    &-icon {
      height: 120px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 auto 10px auto;
      img {
        max-width: 100%;
        max-height: 100%;
      }
    }
    &-title {
      font-size: 24px;
      line-height: 120%;
      font-weight: 700;
    }
  }
  &__btn {
    height: 52px;
    padding: 5px 20px;
    margin: 0 auto;
    span {
      font-size: 16px;
      letter-spacing: 1px;
    }
  }
}
.contacts {
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 32px;
    border-bottom: 1px solid #515D69;
    padding: 80px 0;
  }
  &__group {
    position: relative;
    z-index: 2;
    max-width: 370px;
    flex-shrink: 0;
    padding-top: 35px;
  }
  &__title {
    font-weight: 800;
    color: #0F2A44;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 32px;
    margin-top: 32px;
  }
  &__item {
    display: flex;
    align-items: start;
    gap: 10px;
    i {
      width: 32px;
      flex-shrink: 0;
      padding-top: 4px;
      img {
        max-width: 100%;
      }
    }
    span {
      font-size: 24px;
      line-height: 150%;
      font-weight: 500;
    }
  }
  &__icon,
  &__icon2,
  &__icon3 {
    position: absolute;
    img {
      max-width: 100%;
    }
  }
  &__icon {
    top: 50px;
    left: 335px;
    max-width: 62px;
  }
  &__icon2 {
    top: 170px;
    left: 470px;
    max-width: 80px;
  }
  &__icon3 {
    top: 380px;
    left: 390px;
    max-width: 82px;
  }
  &__form {
    position: relative;
    z-index: 2;
    max-width: 700px;
    width: 100%;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 8px 16px 0px rgba(211, 219, 245, 1);
    border-radius: 20px;
    padding: 36px 32px;
  }
  .form {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    &-title {
      text-align: center;
      margin-bottom: 4px;
    }
    &-subtitle {
      max-width: 510px;
      text-align: center;
      font-size: 24px;
      line-height: 120%;
      font-weight: 500;
      margin: 0 auto 16px auto;
    }
    &-group {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -1px;
        width: 100%;
        height: 48px;
        background: linear-gradient(to top, #00B7FF, #003CA7);
        border-radius: 14px;
        pointer-events: none;
      }
      input {
        width: calc(100% - 2px);
        height: 46px;
        background: #fff;
        border: none;
        border-radius: 14px;
        padding: 0 23px;
        margin-left: 1px;
      }
    }
    &-btn {
      margin-top: 8px;
    }
  }
}

