.footer {
  padding: 40px 0;
  .container {
    max-width: 1410px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
  }
  &__logo {
    width: 216px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    li:not(:last-child) {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(0, -50%);
        width: 2px;
        height: 40px;
        background: #0F2A44;
        border-radius: 3px;
      }
    }
    li a {
      padding: 0 32px;
      font-size: 18px;
      line-height: 155%;
      font-weight: 700;
      letter-spacing: 1.26px;
      text-transform: uppercase;
      color: #515D69;
    }
  }
  &__logo2 {
    width: 200px;
    display: block;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 1400px) {
  .footer {
    &__inner {
      gap: 16px;
    }
    &__logo {
      width: 120px;
    }
    &__list {
      li:not(:last-child) {
        &:after {
          width: 1px;
          height: 24px;
        }
      }
      li a {
        padding: 0 16px;
        font-size: 15px;
        letter-spacing: 1.05px;
      }
    }
    &__logo2 {
      width: 168px;
    }
  }
}

@media screen and (max-width: 1023px) {
  .footer {
    padding: 30px 0;
    &__inner {
      flex-direction: column;
      align-items: start;
      gap: 24px;
    }
    &__logo {
      display: none;
    }
    &__list {
      max-width: 330px;
      flex-wrap: wrap;
      gap: 8px 0;
      li a {
        font-size: 14px;
        letter-spacing: 0;
        &:after {
          height: 16px;
        }
      }
      li:nth-child(1),
      li:nth-child(4) {
        a {
          padding-left: 0;
        }
      }
      li:nth-child(3) {
        &:after {
          display: none;
        }
      }
    }
    &__logo2 {
      width: 152px;
    }
  }
}

@media screen and (max-width: 360px) {
  .footer {
    &__list {
      li {
        &:after {
          height: 12px !important;
        }
      }
      li a {
        font-size: 12px;
      }
    }
  }
}