.wrapper {
  position: relative;
  overflow: hidden;
}
.btn-blue {
  background: #00B8FF;
  box-shadow: 2px 2px 8px 0px rgba(3, 60, 93, 0.3);
  border-radius: 14px;
  &:hover {
    background: #00AAFF;
  }
  &:active {
    background: #0092ED;
  }
  span {
    line-height: 100%;
    font-weight: 700;
    text-transform: uppercase;
    color: #fff;
  }
}
.btn-white {
  background: rgba(255, 255, 255, 0.3);
  border: 2px solid #fff;
  box-shadow: 2px 2px 8px 0px rgba(3, 60, 93, 0.3);
  border-radius: 14px;
  &:hover {
    background: rgba(255, 255, 255, 0.5);
  }
  &:active {
    background: rgba(255, 255, 255, 0.8);
  }
  span {
    line-height: 100%;
    font-weight: 700;
    text-transform: uppercase;
    color: #0F2A44;
  }
}
.h3 {
  font-size: 28px;
  line-height: 120%;
  font-weight: 700;
  letter-spacing: 1.96px;
  text-transform: uppercase;
}
.form {
  .input-title {
    display: block;
    font-size: 18px;
    line-height: 155%;
    font-weight: 500;
    margin-bottom: 4px;
  }
  &-group {
    input {
      position: relative;
      z-index: 2;
      width: 100%;
      height: 48px;
      background: #fff;
      border: 1px solid #0F2A44;
      border-radius: 14px;
      padding: 0 24px;
      font-size: 18px;
      line-height: 155%;
      font-weight: 500;
      color: #1C1F26;
      &::placeholder {
        color: #9D9D9D;
      }
    }
  }
  &-btn__wrap {
    display: flex;
    justify-content: center;
  }
  &-btn {
    height: 48px;
    padding: 5px 20px;
    span {
      font-size: 16px;
      letter-spacing: 0.16px;
    }
  }
  .select {
    position: relative;
    z-index: 3;
    &--active {
      z-index: 4;
      .select-arrow {
        svg {
          transform: rotate(-180deg);
        }
      }
      .select-hidden {
        transform: translateY(0);
        opacity: 1;
        pointer-events: auto;
      }
    }
    &--color {
      .select-current {
        color: #1C1F26;
      }
    }
    &-show {
      position: relative;
      z-index: 2;
      width: 100%;
      height: 48px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 8px;
      background: #fff;
      border: 1px solid #0F2A44;
      border-radius: 14px;
      padding: 0 24px;
      font-size: 18px;
      line-height: 155%;
      font-weight: 500;
      cursor: pointer;
    }
    &-current {
      color: #9D9D9D;
    }
    &-arrow {
      width: 14px;
      svg {
        width: 100%;
        height: auto;
        transition: all 0.3s;
      }
    }
    &-hidden {
      position: absolute;
      top: 100%;
      left: 0;
      transform: translateY(-4px);
      width: 100%;
      padding-top: 4px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.125s;
    }
    &-items {
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
      background: #fff;
      border: 1px solid #0F2A44;
      border-radius: 14px;
      padding: 24px;
    }
    &-item {
      cursor: pointer;
    }
  }
  .calendar {
    &-group {
      position: relative;
    }
    &-input {
      position: relative;
      z-index: 2;
      width: 100%;
      height: 48px;
      background: #fff;
      border: 1px solid #0F2A44;
      border-radius: 14px;
      padding: 0 24px;
      font-size: 18px;
      line-height: 155%;
      font-weight: 500;
      color: #1C1F26;
      &::placeholder {
        color: #9D9D9D;
      }
    }
    &-icon {
      position: absolute;
      z-index: 3;
      top: 50%;
      right: 24px;
      transform: translate(0, -50%);
      width: 20px;
      pointer-events: none;
      img {
        max-width: 100%;
      }
    }
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.6);
  padding: 15px 0;
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 100px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    width: calc(100% - 30px);
    margin: 0 auto;
  }
  &__close {
    position: absolute;
    z-index: 2;
    top: 35px;
    right: 35px;
    width: 21px;
    padding: 0;
    svg {
      width: 100%;
      height: auto;
    }
  }
}
.modal-form {
  .modal__dialog {
    max-width: 706px;
    background-image: url("../img/modal-img.webp");
    background-color: #fff;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 20px;
    padding: 50px 75px 95px 75px;
  }
  .form {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    &-title {
      text-align: center;
      font-weight: 800;
      color: #192A43;
      margin-bottom: 16px;
    }
    &-subtitle {
      max-width: 412px;
      text-align: center;
      font-size: 18px;
      line-height: 120%;
      font-weight: 500;
      margin: 0 auto 50px auto;
    }
    &-btn__wrap {
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .h3 {
    font-size: 24px;
    letter-spacing: 0.36px;
  }
}

@media screen and (max-width: 1023px) {
  .h3 {
    font-size: 20px;
    letter-spacing: 1.4px;
  }
  .form {
    .input-title {
      font-size: 16px;
    }
  }
  .modal {
    &__close {
      top: 30px;
      right: 30px;
    }
  }
  .modal-form {
    .modal__dialog {
      padding: 75px 25px;
    }
    .form {
      gap: 16px;
      &-title {
        margin-bottom: 16px;
      }
      &-subtitle {
        max-width: 330px;
        font-size: 16px;
        margin-bottom: 24px;
      }
      &-btn__wrap {
        margin-top: 8px;
      }
    }
  }
}